.layout-main {
  padding: 2rem;
  background-color: #f9f5ff;
  min-height: 100vh;
}

/* ðŸ”¹ Intro Section Layout */
.intro-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
  padding: 3rem;
  margin: 2rem auto;
  background: white;
  border-radius: 20px;
  box-shadow: 0 4px 12px rgba(56, 1, 92, 0.15);
  max-width: 1200px;
}

/* ðŸ”¹ Text Section */
.intro-text {
  flex: 1;
}

.intro-text h1 {
  font-size: 2.5rem;
  color: #38015c;
  margin-bottom: 1rem;
}

.intro-text p {
  font-size: 1.1rem;
  color: #4a4a4a;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.intro-text ul {
  list-style: none;
  padding: 0;
  margin-bottom: 2rem;
}

.intro-text li {
  font-size: 1rem;
  margin: 0.5rem 0;
  color: #555;
}

/* ðŸ”¹ Button */
.get-started-btn {
  background-color: #38015c;
  color: #ffffff;
  border: none;
  padding: 0.8rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(194, 145, 226, 0.4);
}

.get-started-btn:hover {
  background-color: #c291e2;
  color: #38015c;
  transform: translateY(-3px);
  box-shadow: 0 0 15px rgba(194, 145, 226, 0.6);
}

/* ðŸ”¹ Images Section */
.intro-images {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-image {
  width: 100%;
  max-width: 450px;
  border-radius: 20px;
  box-shadow: 0 4px 10px rgba(56, 1, 92, 0.2);
  margin-bottom: 1.5rem;
}

.image-gallery {
  display: flex;
  gap: 1rem;
}

.image-gallery img {
  width: 48%;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(56, 1, 92, 0.15);
}

/* ðŸ”¹ Responsive Design */
@media (max-width: 900px) {
  .intro-section {
    flex-direction: column;
    text-align: center;
    padding: 2rem;
  }

  .intro-images {
    flex-direction: column;
  }

  .main-image {
    max-width: 100%;
  }

  .image-gallery {
    flex-direction: row;
    justify-content: center;
  }

  .get-started-btn {
    width: 70%;
    margin: 0 auto;
  }
}
